import Validator from './base.validator'

export default class ArrayValidator extends Validator {

  constructor(options) {
    super(options);

    const attributes = {
      message: '{attribute}',
      max: null,
      message_max: '{attribute}{max}',
      min: null,
      message_min: '{attribute}{min}',
    };

    this.parse(attributes, options);
  }


  validateValue(value) {
    do {
      if (Array.isArray(value)) {
        if (this.max !== null && value.length > this.max) {
          return this.message_max
        }
        if (this.min !== null && value.length < this.min) {
          return this.message_min
        }
        break;
      };

      return this.message
    } while (false);

    return null;
  }

}


ArrayValidator.type = 'array';
