import Validator from './base.validator'

export default class RequiredValidator extends Validator {

  constructor(options) {
    super(options);

    this.message = '{attribute}';
  }

  validateValue(value) {
    do {
      if (0 === value) {
        break;
      }

      // 
      if (Array.isArray(value)) {
        if (value.length > 0) break;
      } else {
        // 
        if (!!value) break;
      }

      return this.message
    } while (false);
    // break
    return null
  }
}

RequiredValidator.type = 'required';
